import { Component, OnInit } from '@angular/core';
import { IExperience } from '@shared/models/experience-model';
import { GoogleDriveProvider } from '@shared/services/google-drive-provider.service';
import { SEOService } from '@shared/services/s-e-o.service';

@Component({
  selector: 'app-experience-list',
  templateUrl: './experience-list.component.html',
  styleUrls: ['./experience-list.component.sass'],
})
export class ExperienceListComponent implements OnInit {

  public experienceModel: IExperience;

  constructor(
    private googleDriveProvider: GoogleDriveProvider,
    private seoService: SEOService,
  ) {}

  ngOnInit(): void {
    this.googleDriveProvider.load().subscribe(data => {
      data.experienceList$.subscribe(data => {
        data.experienceList.sort(((a, b) =>
          a.projectDateRange.to > b.projectDateRange.to ? -1 : 1
        ))
        this.experienceModel = data
      })
      data.staticPageList$.subscribe(data => {
        this.seoService.updateMeta(
          data.pageList.find(item => item.name == 'experience').seoMeta,
          {isStaticPage: true, isArticle: false}
        )
      })
    })
  }
}
